@import '../../../../shared/styles/const';

.profile {
  background: url('purple.png') no-repeat 50% 50%;
  background-size: cover;
  height: $g_height;
  display: flex;
  flex-direction: column;
  align-items: center;

  img {
    margin: 115px 0 45px; }

  &__text {
    text-align: center;
